@import url(https://fonts.googleapis.com/css?family=Dekko);

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.center{
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

/* improve this  */
.button{ 
  max-height: 3em;
  font-size: 1.1em;
  
}

a {
  color: black;

}

a:hover {
  color: blue;
}
.NavBar {
    list-style-type: none;
    margin: 0;
    padding: 0;
    display: flex;
    background: #7FFFD4;
}

li a {
    display: block;
    color: black;
    text-align:center;
    padding: 14px 16px;
    text-decoration:none;
}

li a:hover {
    background-color: blue;
    color: white !important;
}

#home{
    background: #55816f;
}

.home_page{
   font-family: Dekko;
}

.profilepic{
   width: 2.5in; 
   height: 2.5in;
   border-radius: 50%;
}

.intro{
   padding: 1%;
   text-align: center;
   border-bottom: 1px solid grey; 
   border-top: 1px solid grey;
}

.afterintro{
   /* border-bottom: 1px solid grey; */
   display: flex;

}

.aboutme{
   width: 8in;
   font-size: 20px;
   padding-right: 2%;
   padding-left:10%;
}

#name_title{
   font-size: 40px;
   padding:0%;
}

#after_intro_head{
   font-size: 25px;
}

.contact{
   border-top: 1px solid grey;;
   text-align: center;
   background-color: #7FFFD4;
}

.skills_area{
   width: 6in;
}

.single_skill{
   display: inline;
}


